<template>
  <!-- DATA VIEW-->
  <div ref="datawrapper">
    <MDBContainer>
      <MDBRow>
        <MDBCol md="12" class="mx-auto">
          <MDBRow>
            <img
              class="mx-auto my-4"
              style="max-width: 500px"
              src="../assets/pollenyzer_logo.svg"
            />
          </MDBRow>
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <Transition
          appear
          name="bounce"
          @after-enter="showAnnotations(pollenData)"
        >
          <p v-if="showCards[0]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardTitle>Pollen Detections</MDBCardTitle>
                <MDBCardText>
                  <ul style="margin-bottom: 2em">
                    <li>{{ pollenData?.pollen?.n_pollen }} pollen detected.</li>
                    <li v-if="pollenData?.pollen?.calibration">
                      Calibration strip found. The extracted and processed color
                      strip is shown in the upper right corner of the image
                      below.
                    </li>
                    <li v-if="!pollenData?.pollen?.calibration">
                      No calibration strip found. Consider to buy a Kellogs(TM)
                      corn flakes packaging which includes a 24 color
                      calibration strip. Other products that are printed on
                      similar paper which contain the very same calibration
                      strip can also be used.
                    </li>
                    <li>
                      Use the zoom function to check on the highlighted
                      detections.
                    </li>
                  </ul>
                  <div
                    id="annotationimgcontainer"
                    ref="annotationimgcontainer"
                  ></div>
                  <img
                    ref="annotationimg"
                    id="annotationimg"
                    style="display: none"
                  />
                  <canvas
                    style="display: none"
                    ref="annotationcanvas"
                    class="annotation-canvas"
                  ></canvas>
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
        <!-- <Transition appear name="bounce">
          <p v-if="showCards[1]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardTitle>Pollen Clusters</MDBCardTitle>
                <MDBCardText>
                  <ul style="margin-bottom: 2em">
                    <li>{{ pollenData?.pollen?.clusters?.n_clusters_predicted }} clusters and
                      {{ pollenData?.pollen?.clusters?.n_outliers_predicted }} outliers detected.</li>
                    <li>For all detected pollen the following dendrogram shows their attributed clusters in different
                      colors. Connections
                      colored in light gray show outliers (that is clusters of size 1).</li>
                  </ul>
                  <img :src="pollenData?.pollen?.clusters?.dendrogram" style="width: 100%; max-width: 600px;" />
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition> -->
        <Transition appear name="bounce">
          <p v-if="showCards[1]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardText>
                  <div
                    style="
                      margin-bottom: 10px;
                      width: 100%;
                      text-align: center;
                      font-weight: bold;
                    "
                  >
                    #cluster {{ nCluster }}, including
                    {{
                      pollenData?.pollen?.clusters?.cluster_imgs[
                        nCluster
                      ].cluster.filter(
                        (v) =>
                          v.cluster_size <
                          Math.ceil(pollenData?.pollen?.n_pollen / 100)
                      ).length
                    }}
                    outlier(s)
                  </div>
                  <vue3-slider
                    trackColor="#ffa820"
                    color="#97be11"
                    :tooltip="true"
                    v-model="nCluster"
                    :handleScale="3"
                    :min="1"
                    :alwaysShowHandle="true"
                    :sticky="true"
                    :max="maxCluster"
                  />
                  <div>
                    <div class="cluster-img-wrapper">
                      <div style="font-weight: bold">Diversity indices</div>
                    </div>
                    <div class="cluster-img-wrapper">
                      <div>
                        <a
                          href="https://en.wikipedia.org/wiki/Diversity_index"
                          target="_blank"
                          >Richness</a
                        >:
                        {{
                          pollenData?.pollen?.clusters?.cluster_imgs[nCluster]
                            ?.diversity?.richness
                        }}
                      </div>
                      <div>
                        <a
                          href="https://en.wikipedia.org/wiki/Diversity_index"
                          target="_blank"
                          >Shannon</a
                        >:
                        {{
                          pollenData?.pollen?.clusters?.cluster_imgs[
                            nCluster
                          ]?.diversity?.shannon?.toFixed(4) || "non-calculable"
                        }},
                      </div>
                      <div>
                        <a
                          href="https://en.wikipedia.org/wiki/Diversity_index"
                          target="_blank"
                          >Evenness</a
                        >:
                        {{
                          pollenData?.pollen?.clusters?.cluster_imgs[
                            nCluster
                          ]?.diversity?.shannon_eveness?.toFixed(4) ||
                          "non-calculable"
                        }},
                      </div>
                      <div>
                        <a
                          href="https://en.wikipedia.org/wiki/Diversity_index"
                          target="_blank"
                          >Avg. cluster size</a
                        >:
                        {{
                          pollenData?.pollen?.clusters?.cluster_imgs[
                            nCluster
                          ]?.diversity?.avg_population_size?.toFixed(2)
                        }}
                      </div>
                    </div>

                    <div class="cluster-img-wrapper">
                      <div style="font-weight: bold">
                        primary pollen colors per cluster
                      </div>
                    </div>
                    <div
                      v-for="(cluster, index) in pollenData?.pollen?.clusters
                        ?.cluster_imgs[nCluster].cluster"
                      class="cluster-img-wrapper"
                      :key="cluster"
                    >
                      <div
                        v-if="
                          index > 1 &&
                          pollenData?.pollen?.clusters?.cluster_imgs[nCluster]
                            .cluster[index - 1].cluster_size >
                            Math.ceil(pollenData?.pollen?.n_pollen / 100) &&
                          cluster.cluster_size <=
                            Math.ceil(pollenData?.pollen?.n_pollen / 100)
                        "
                      >
                        <div
                          style="
                            text-align: center;
                            margin-bottom: -50px;
                            color: #888;
                          "
                        >
                          outliers (n less than 1 percent)
                        </div>
                        <hr class="outliers" />
                      </div>
                      <div style="color: #888">
                        cluster {{ index + 1 }} (n={{ cluster.cluster_size }})
                      </div>
                      <img
                        :src="cluster.cluster_primary_canvas"
                        class="cluster-img"
                      />
                    </div>
                  </div>
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
      </MDBRow>
    </MDBContainer>
    <CamFooter
      @nextCard="nextCard()"
      @previousCard="previousCard()"
      @redo="$emit('redo')"
      :prevDisabled="prevDisabled"
      :nextDisabled="nextDisabled"
      :redoDisabled="false"
    />
  </div>
</template>

<style>
hr.outliers {
  border-top: 5px dotted #888;
  color: #fff;
  background-color: #fff;
  margin-bottom: 5px;
  margin-top: 50px;
}

.vue3-slider {
  margin-bottom: 40px;
}

.cluster-img-wrapper {
  text-align: center;
}

.cluster-img {
  image-rendering: pixelated;
  width: 30%;
  max-width: 200px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5%;
  display: inline-block;
  font-weight: bold;
}

.annotation-canvas {
  width: 100%;
}

.bar-canvas {
  width: 100%;
  height: 70%;
}

.card-body {
  padding-bottom: 70px;
}
</style>

<script>
/* eslint-disable */
import {
  MDBCol,
  MDBRow,
  MDBContainer,
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
} from "mdb-vue-ui-kit";
import CamFooter from "@/components/CamFooter.vue";
import slider from "vue3-slider";
import Zoomist from "zoomist";

export default {
  components: {
    MDBCol,
    MDBRow,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    CamFooter,
    "vue3-slider": slider,
  },
  props: {
    pollenData: Object,
  },
  data() {
    return {
      showCards: [true, false, false],
      cardId: 0,
      nCluster: 1,
      maxCluster: 1,
      prevDisabled: true,
      nextDisabled: false,
    };
  },
  mounted() {},
  watch: {
    pollenData(value, oldValue) {
      this.showAnnotations(value);
    },
  },
  methods: {
    showAnnotations(pollenData) {
      if (pollenData) {
        this.nCluster =
          this.pollenData.pollen.clusters.n_clusters_predicted +
          this.pollenData.pollen.clusters.n_outliers_predicted;
        this.maxCluster =
          Object.keys(this.pollenData?.pollen?.clusters?.cluster_imgs).length || 1;
        console.log(
          "Maxclsuter",
          this.maxCluster,
          this.pollenData?.pollen?.clusters,
          this.pollenData?.pollen?.clusters.cluster_imgs,
          this.pollenData?.pollen?.clusters.length
        );
        let annotationCanvas = this.$refs.annotationcanvas;
        let annotationImg = this.$refs.annotationimg;
        const context = annotationCanvas.getContext("2d");
        context.canvas.width = pollenData.pollen.img_width;
        context.canvas.height = pollenData.pollen.img_height;
        var image = new Image();
        image.onload = function () {
          context.drawImage(
            image,
            0,
            0,
            pollenData.pollen.img_width,
            pollenData.pollen.img_height
          );
          annotationImg.src = annotationCanvas.toDataURL();
          new Zoomist("#annotationimgcontainer", {
            src: annotationImg,
            maxRatio: 10,
            slider: false,
            zoomer: true,
          });
        };
        //image.src = pollenData.dataURL
        image.src = pollenData.pollen.annotated_img;
      }
    },
    nextCard() {
      this.showCards[this.cardId] = false;
      this.cardId++;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 1) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
    previousCard() {
      this.showCards[this.cardId] = false;
      this.cardId--;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 1) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
  },
};
</script>


