<template>
  <!-- MAIN WRAPPER-->
  <div style="height:100%; width: 100%">
    <div style="overflow:hidden" ref="errorcontainer" v-if="errorMsg" class="error-container parent">
      <div class="error-close">
        <a style="color: #fbfceb;" href="#!" @click="(event) => { event.preventDefault(); this.errorMsg = null }"
          role="button">
          <i class="fa fa-times fa-2x"></i>
        </a>
      </div>
      <div class="error-content">{{ errorMsg }}</div>
    </div>
    <div v-if="!$route.params?.uploaded_image" ref="maincamcontainer" style="height: 100%; display:flex">
      <MainCam @error="(errorMsg) => onError(errorMsg)" :metaData="$route.params?.metaData"
        @photoAnalyzed="(data) => onPhotoAnalyzed(data)"></MainCam>
    </div>
    <div v-if="$route.params?.uploaded_image" ref="mainfilecontainer" style="height: 100%; display:flex">
      <MainFile @error="(errorMsg) => onError(errorMsg)" :metaData="$route.params?.metaData"
        @photoAnalyzed="(data) => onPhotoAnalyzed(data)"></MainFile>
    </div>
    <div ref="maindatacontainer" style="display: none;">
      <MainData @redo="onRedo()" @error="(errorMsg) => onError(errorMsg)" :pollenData="pollenData"></MainData>
    </div>
  </div>
</template>


<style scoped lang="scss">
@import '@/assets/colors.scss';

.parent {
  display: grid;
  grid-template-columns: 1fr 24px;
  grid-template-rows: auto 1fr;
  grid-template-areas: "header-left header-right";
}

.error-content {
  grid-area: header-left;
}

.error-close {
  overflow: hidden;
  width: 100%;
  min-height: 10px;
  grid-area: header-right;
}

.error-container {
  border-radius: 10px;
  background: rgba(220, 30, 11, 1);
  color: #fbfceb; //rgb(220, 30, 11);
  padding: 20px;
  height: 150px;
  width: 90%;
  margin-top: 5%;
  margin-left: 5%;
  font-weight: bolder;
  position: absolute
}
</style>


<script>
/* eslint-disable */
import MainCam from "@/components/MainCam.vue"
import MainData from "@/components/MainData.vue"
import MainFile from "@/components/MainFile.vue"
import "mdb-vue-ui-kit";

export default {
  components: {
    MainCam,
    MainData,
    MainFile,
  },
  props: {
  },
  created() {
  },
  mounted() { },
  setup() {
  },
  data() {
    return {
      metaData: undefined,
      pollenData: undefined,
      errorMsg: null,
      errorZIndex: 10,
    };
  },
  methods: {
    onRedo() {
      this.$router.push("/")

    },
    onError(errorMsg) {
      this.errorMsg = errorMsg;
      setTimeout(() => { this.$refs.errorcontainer.style.zIndex = ++this.errorZIndex; }, 200)
    },
    onPhotoAnalyzed(data) {
      try {
        this.$refs.maincamcontainer.style.display = "none"
      } catch { }
      try {
        this.$refs.mainfilecontainer.style.display = "none"
      } catch { }
      this.$refs.maindatacontainer.style.display = "block"
      this.pollenData = data
    },
  },

}
</script>
