<template>
  <div>
    <MDBContainer>
      <MDBRow>
        <MDBCol md="12" class="mx-auto">
          <MDBRow>
            <img class="mx-auto my-4" style="max-width: 500px" alt="Vue logo" src="../assets/pollenyzer_logo.svg" />
          </MDBRow>
          <MDBRow> </MDBRow>
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <Transition appear name="bounce">
          <p v-if="showCards[0]">
            <MyCard1 />
          </p>
        </Transition>
        <Transition name="bounce">
          <p v-if="showCards[1]">
            <MyCard2 ref="metadata" />
          </p>
        </Transition>
        <Transition name="bounce">
          <p v-if="showCards[2]">
            <MyCard3 :metaData="metaData" />
          </p>
        </Transition>
      </MDBRow>
    </MDBContainer>
    <MyFooter @nextCard="nextCard()" @previousCard="previousCard()" :prevDisabled="prevDisabled"
      :nextDisabled="nextDisabled" />
  </div>
</template>

<script>
// @ is an alias to /src
import MyFooter from "@/components/MyFooter.vue";
import MyCard1 from "@/components/MyCard1.vue";
import MyCard2 from "@/components/MyCard2.vue";
import MyCard3 from "@/components/MyCard3.vue";
import { MDBCol, MDBRow, MDBContainer } from "mdb-vue-ui-kit";

export default {
  data() {
    return {
      cardId: 0,
      showCards: [true, false, false],
      prevDisabled: true,
      nextDisabled: false,
      metaData: undefined
    };
  },
  methods: {
    nextCard() {
      this.showCards[this.cardId] = false;
      this.cardId++;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {

        this.metaData = {
          "hiveCoords": this.$refs.metadata.hiveCoords,
          "trapOpenedDate": this.$refs.metadata.trapOpenedDate,
          "habitat": this.$refs.metadata.habitat,
          "daysMounted": this.$refs.metadata.daysMounted,
          "privacyChecked": this.$refs.metadata.privacyChecked
        }
        if (!this.metaData.privacyChecked) {
          // require privacy agreement
          this.showCards[this.cardId] = false
          this.cardId--;
          this.showCards[this.cardId] = true
          return
        }
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
    previousCard() {
      this.showCards[this.cardId] = false;
      this.cardId--;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
  },
  name: "HomeView",
  components: {
    MyFooter,
    MyCard1,
    MyCard2,
    MyCard3,
    MDBCol,
    MDBRow,
    MDBContainer,
  },
};
</script>

<style>
.card-body {
  padding-bottom: 70px;
}

.bounce-enter-active {
  animation: bounce-in 0.5s;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}
</style>
