<template>
  <!-- CAM VIEW -->
  <div class="wrapper align-items-center justify-content-center" style="display:flex">
    <canvas ref="videocanvas" class="video-canvas"></canvas>
    <div ref="particlediv" class="particle-div"></div>
    <video class="video" ref="videoviewer" />
    <div class="photo-button-container d-block text-center my-3">
      <button class="button photo-button" :disabled="camButtonDisabled" @click="TakePhoto">
        <i class="fas fa-camera" style=""></i>
      </button>
    </div>
  </div>
</template>

<script>
import "mdb-vue-ui-kit";
import { ParticleNetwork } from '@/assets/particles'

export default {
  components: {},
  data() {
    return {
      mediaStream: null,
      data: null,
      camButtonDisabled: false,
    }
  },
  props: {
    metaData: undefined
  },
  methods: {
    async StartRecording() {
      await navigator.mediaDevices.enumerateDevices().catch(
        (err) => { this.$emit("error", "Could not retrieve cameras. (" + err + ")") }
      );
      let video = this.$refs.videoviewer;
      let canva = this.$refs.videocanvas;
      let particleDiv = this.$refs.particlediv;
      this.camButtonDisabled = false

      canva.style.display = "none"
      particleDiv.display = "none"
      video.style.display = "block"
      this.mediaStream = navigator.mediaDevices.getUserMedia({
        video: {
          facingMode: "environment",
          width: { ideal: 2048 },
          height: { ideal: 1536 }
        },
      })
        .catch(
          (err) => { this.$emit("error", "Could not start camera. (" + err + ")") }
        )
        .then((mediaStream) => {
          this.mediaStream = mediaStream;
          video.srcObject = this.mediaStream;
          return video.play()
            .catch(
              (err) => { this.$emit("error", "Could not start playing camera stream. (" + err + ")") }
            );
        });
    },
    async TakePhoto() {
      let video = this.$refs.videoviewer;
      let canva = this.$refs.videocanvas;
      let particleDiv = this.$refs.particlediv;
      this.camButtonDisabled = true;

      const context = canva.getContext('2d');
      context.canvas.width = video.videoWidth;
      context.canvas.height = video.videoHeight
      let videoWidth = video.videoWidth;
      let videoHeight = video.videoHeight
      context.drawImage(video, 0, 0, videoWidth, videoHeight);
      let videoClientWidth = video.clientWidth
      let dataURL = canva.toDataURL()

      let aspectRatio = videoHeight / videoWidth
      video.style.display = "none"
      canva.style.display = "block"
      canva.style.width = "100%"
      particleDiv.style.display = "block"
      particleDiv.innerHTML = "";

      // TODO Listen to canva clientWidth changes and update width and height
      particleDiv.style.width = videoClientWidth + "px";
      particleDiv.style.height = videoClientWidth * aspectRatio + "px";

      var options = {
        particleColor: '#ffa820', // #ffa820 (orange)
        background: dataURL,
        interactive: true,
        speed: 'fast',
        density: 'high'
      };

      new ParticleNetwork(particleDiv, options);

      const requestOptions = {
        method: "POST",
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
        body: JSON.stringify({ img: dataURL, metadata: this.metaData })
      };

      const tracks = this.mediaStream?.getVideoTracks();
      tracks?.forEach((track) => {
        track.stop();
      });

      fetch("https://us-east1-pollenyzer-98b13.cloudfunctions.net/analyze123", requestOptions)
        .then(response => {
          if (!response.ok) {
            throw new Error(JSON.stringify(response.json()))
          }
          let data = response.json()
          particleDiv.innerHTML = ""
          particleDiv.style.display = "none"
          return data
        })
        .then(data => {
          if (!data) {
            const msg = "Could contact AI-server."
            console.log(msg)
            throw new Error(msg)
          }
          data["dataURL"] = dataURL
          data["imgWidth"] = videoWidth
          data["imgHeight"] = videoHeight
          this.$emit("photoAnalyzed", data)
        })
        .catch((err) => {
          console.log("server error", err)
          this.$emit("error", "Could not retrieve pollen analysis. (" + err + ")");
          particleDiv.innerHTML = "";
          particleDiv.style.display = "none"
          this.$router.push({
            name: "main", //use name for router push
            params: {}
          });

        });
    },
  },
  async mounted() {
    await this.StartRecording().catch(
      (err) => { this.$emit("error", "Could not start camera. (" + err + ")") }
    );
  },
}
</script>


