<script>
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
} from "mdb-vue-ui-kit";

export default {
  name: "MyCard3",
  data() {
    return {
      date: null,
    };
  },
  components: {
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
  },
  props: {
    metaData: undefined
  },
  mounted() {
    const fileupload = this.$refs.fileupload;
    fileupload.onclick = function () {
      this.value = null;
    };
    const router = this.$router
    var vm = this
    fileupload.addEventListener("change", function () {
      const reader = new FileReader();
      reader.addEventListener("load", function () {
        const uploaded_image = reader.result;
        router.push({
          name: "main",
          params: { uploaded_image: uploaded_image, metaData: JSON.stringify(vm.metaData)}
        });
      });
      reader.readAsDataURL(this.files[0]);
    });
  },
  methods: {
    openCam(){
      this.$router.push({name: 'main', params: {metaData: JSON.stringify(this.metaData)}})
    }
  }
};
</script>

