<template>
  <!-- CAM VIEW -->
  <div class="wrapper align-items-center justify-content-center" style="display:flex">
    <div ref="particledivfile" class="particle-div"></div>
    <img class="image" ref="imgviewer" :src="this.$route.params.uploaded_image" />

    <div class="photo-button-container d-block text-center my-3">
      <button class="button photo-button" :disabled="camButtonDisabled" @click="analyzeUpload">
        <i class="fas fa-play" style=""></i>
      </button>

    </div>
  </div>
</template>

<script>
/* eslint-disable */
import "mdb-vue-ui-kit";
import { ParticleNetwork } from '@/assets/particles'

export default {
  components: {},
  data() {
    return {
      data: null,
      camButtonDisabled: false,
    }
  },
  props: {
    metaData: undefined
  },
  methods: {
    async analyzeUpload() {
      let image = this.$refs.imgviewer;
      let imageWidth = image.width;
      let imageHeight = image.height;

      let particleDiv = this.$refs.particledivfile;
      let dataURL = this.$route.params.uploaded_image
      this.camButtonDisabled = true

      particleDiv.style.display = "block"
      particleDiv.innerHTML = "";
      particleDiv.style.width = "100%";
      let aspectRatio = imageHeight / imageWidth
      particleDiv.style.display = "block"
      particleDiv.innerHTML = "";
      particleDiv.style.width = image.clientWidth + "px";
      particleDiv.style.height = image.clientWidth * aspectRatio + "px";

      var options = {
        particleColor: '#ffa820', // #ffa820 (orange)
        background: dataURL,
        interactive: true,
        speed: 'fast',
        density: 'high'
      };

      new ParticleNetwork(particleDiv, options);

      const requestOptions = {
        method: "POST",
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
        body: JSON.stringify({ img: dataURL, metadata: this.metaData })
      };

      fetch("https://us-east1-pollenyzer-98b13.cloudfunctions.net/analyze123", requestOptions)
        .then(response => {
          if(!response.ok){
            throw new Error(JSON.stringify(response.json()))
          }
          let data = response.json()
          particleDiv.innerHTML = ""
          particleDiv.style.display = "none"
          return data
        })
        .then(data => {
          if (!data) {
            const msg = "Could contact AI-server."
            console.log(msg)
            throw new Error(msg)
          }
          console.log(data)
          data["dataURL"] = dataURL
          data["imgWidth"] = imageWidth
          data["imgHeight"] = imageHeight
          this.camButtonDisabled = false
          this.$emit("photoAnalyzed", data)

        })
        .catch((err) => {
          this.$emit("error", "Could not retrieve pollen analysis. (" + err + ")");
          particleDiv.innerHTML = "";
          particleDiv.style.display = "none"
          router.push({
            name: "main", //use name for router push
            params: { uploaded_image: uploaded_image }
          });
        });
    },
  },
  async mounted() { },
}
</script>


